import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

export interface DialogData {
  produto: string;
  observacao: string;
  status: boolean;
}

@Component({
  selector: 'app-observacao-item',
  templateUrl: './observacao-item.component.html',
  styleUrls: ['./observacao-item.component.css']
})
export class ObservacaoItemComponent implements OnInit {

  constructor(
    public dialogRef: MatDialogRef<ObservacaoItemComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) { }

  onNoClick(): void {
    this.data.status = false;
    this.dialogRef.close(this.data);
  }

  onYesClick(): void {
    this.data.status = true;
    this.dialogRef.close(this.data);
  }
  ngOnInit(): void {
  }

}
