import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';

export interface DialogConfirmacao {
  mensagem: string;
  resposta: string;
  seMotivo: boolean;
  status: boolean;
}

@Component({
  selector: 'app-popup-confirmacao',
  templateUrl: './popup-confirmacao.component.html',
  styleUrls: ['./popup-confirmacao.component.css']
})
export class PopupConfirmacaoComponent implements OnInit {

  constructor(private snackBar: MatSnackBar,
    public dialogRef: MatDialogRef<PopupConfirmacaoComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogConfirmacao) { }

  ngOnInit(): void {
  }
  onNoClick(): void {
    this.data.status = false;
    this.dialogRef.close(this.data);
  }

  onYesClick(): void {
    if (this.data.seMotivo && !this.data.resposta) {
      this.snackBar.open(`Informe o motivo!`, null, { duration: 3000 });
      return;
    }
    this.data.status = true;
    this.dialogRef.close(this.data);
  }
}
