import { Component, ViewChild, ElementRef, OnInit, Injectable } from '@angular/core';
import { dxb } from '../db'
import { ObservacaoItemComponent } from '../observacao-item/observacao-item.component';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar'
import { AngularFireDatabase } from '@angular/fire/database';
import { PopupConfirmacaoComponent } from '../popup-confirmacao/popup-confirmacao.component';

@Component({
  selector: 'app-inicio',
  templateUrl: './inicio.component.html',
  styleUrls: ['./inicio.component.css']
})
@Injectable()
export class InicioComponent implements OnInit {
  @ViewChild('divLogo', { static: true })
  divLogo: ElementRef<HTMLCanvasElement>;
  private ctx: CanvasRenderingContext2D;

  title = 'Sabor & Afeto';
  steps: any = [];
  itemObservacao: any;
  menuAtual: any;
  nomeCliente = '';
  telefoneCliente = '';
  horaRetirada = 0;
  minutoRetirada = 0;
  tiles: any;
  bag: any = [];
  qtdPedido = 0;


  constructor(public dialog: MatDialog, private snackBar: MatSnackBar, private db: AngularFireDatabase) {
    this.tiles = [];
    // this.menuAtual = dxb.Menu;

    this.db.list('saborAfeto/menu').query.once("value").then(data => {
      this.tiles = data.val();
      this.menuAtual = this.tiles;
    });
    this.db.list(`saborAfeto/${this.dataAtualFormatada()}`).valueChanges().subscribe(data => this.qtdPedido = data.length + 1);
  }

  ngOnInit(): void {
    if (this.divLogo) {
      this.ctx = this.divLogo.nativeElement.getContext('2d');
      var ctx = this.ctx;
      var img = new Image();
      var self = this;
      img.src = "../assets/image/logo.jpg";
      img.onload = function () {


        ctx.beginPath();
        ctx.arc(100, 100, 60, 0, 6.28, false); //draw the circle
        ctx.clip(); //call the clip method so the next render is clipped in last path
        ctx.closePath();
        ctx.drawImage(img, 25, 50);
      };
    }
  }

  clickDivSelecao(tile) {
    if (tile.subMenu) {
      this.steps.push(this.menuAtual)
      this.menuAtual = tile.subMenu;
      this.tiles = tile.subMenu;
    }
    else if (tile.itens) {
      this.steps.push(this.menuAtual)
      this.tiles = tile.itens;
    }
    else if (tile.valor) {
      var idPedidoItem = Math.max.apply(Math, this.bag.map(function (o) { return o.idPedidoItem; }));
      if (idPedidoItem == Number.POSITIVE_INFINITY || idPedidoItem == Number.NEGATIVE_INFINITY)
        idPedidoItem = 0;
      idPedidoItem = idPedidoItem + 1;

      this.bag.push({ idPedidoItem, ...tile }); this.tiles = dxb.Menu;
      this.menuAtual = dxb.Menu;
      this.steps = [];
    }
    else {
      this.tiles = dxb.Menu;
      this.steps = [];
      this.menuAtual = dxb.Menu;;
    }

  }

  clickDivRemoveItem(item) {
    console.log(item)
    const dialogRef = this.dialog.open(PopupConfirmacaoComponent, {
      width: '250px',
      data: { mensagem: `Deseja remover o item: ${item.descr} ?` }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      if (result.status) {
        this.bag = this.bag.filter((x) => x != item);
      }
    });
    // let snackBarRef = this.snackBar.open(`Deseja remover ${item.descr} ?`, 'Confirmar', { duration: 3000 });;
    // snackBarRef.onAction().subscribe(() => {
    //   this.bag = this.bag.filter((x) => x != item);
    // });
  }

  clickDivMessageItem(item) {
    this.itemObservacao = item;
    const dialogRef = this.dialog.open(ObservacaoItemComponent, {
      width: '250px',
      data: { produto: item.descr, observacao: item.observacao }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      if (result.status)
        this.itemObservacao.observacao = result.observacao;
    });

  }

  voltarMenu() {
    this.tiles = this.steps.pop();
  }

  formatarValor(value) {
    var parts = value.toFixed(2).split(".");
    parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ".");
    return "R$ " + parts.join(",");
  }

  valorBag() {
    if (this.bag.length == 0)
      return this.formatarValor(0);
    else
      return this.formatarValor(this.bag.reduce((ac, va) => ac + va.valor, 0));
  }

  btnFinalizar() {
    if (!this.nomeCliente) {
      this.snackBar.open(`Nome do cliente obrigatório!`, null, { duration: 3000 });
      return;
    }
    
    if (this.horaRetirada <= 0) {
      this.snackBar.open(`Hora de retirada obrigatório!`, null, { duration: 3000 });
      return;
    }

    if (this.minutoRetirada <= 0) {
      this.snackBar.open(`Minuto de retirada obrigatório!`, null, { duration: 3000 });
      return;
    }

    if (this.bag.length <= 0) {
      this.snackBar.open(`Deve ter ao menos 1 item no pedido!`, null, { duration: 3000 });
      return;
    }

    var dataAtual = this.dataAtualFormatada();
    var xx = this.db.database.ref('saborAfeto');
    xx.child(`${dataAtual}/${this.qtdPedido}`).set({
      idPedido: this.qtdPedido,
      data: dataAtual,
      nomeCliente: this.nomeCliente,
      telefoneCliente: this.telefoneCliente,
      horaRetirada: this.horaRetirada,
      minutoRetirada: this.minutoRetirada,
      itens: this.bag,
      status: 0
    });

    let snackBarRef = this.snackBar.open(`Pedido enviado para a cozinha`, null, { duration: 3000 });
    // snackBarRef.onAction().subscribe(() => {
    //   this.bag = this.bag.filter((x) => x != item);
    // });

    this.limparCampos();
  }

  btnCancelar() {
    const dialogRef = this.dialog.open(PopupConfirmacaoComponent, {
      width: '250px',
      data: { mensagem: "Deseja cancelar o pedido?", seMotivo: true }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      if (result.status) {
        var dataAtual = this.dataAtualFormatada();
        var xx = this.db.database.ref('saborAfeto');
        xx.child(`${dataAtual}/${this.qtdPedido}`).set({
          idPedido: this.qtdPedido,
          data: dataAtual,
          nomeCliente: this.nomeCliente,
          telefoneCliente: this.telefoneCliente,
          horaRetirada: this.horaRetirada,
          minutoRetirada: this.minutoRetirada,
          itens: this.bag,
          motivo: result.resposta,
          status: 2
        });
        this.limparCampos();
      }
    });
  }

  limparCampos() {
    this.bag = [];
    this.nomeCliente = '';
    this.telefoneCliente = '';
    this.horaRetirada = 0;
    this.minutoRetirada = 0;
  }

  dataAtualFormatada() {
    var data = new Date(),
      dia = data.getDate().toString(),
      diaF = (dia.length == 1) ? '0' + dia : dia,
      mes = (data.getMonth() + 1).toString(), //+1 pois no getMonth Janeiro começa com zero.
      mesF = (mes.length == 1) ? '0' + mes : mes,
      anoF = data.getFullYear();
    return anoF + "/" + mesF + "/" + diaF;
  }

  carregarLogo() {
    var ctx;
    var canvas;

    var img = new Image();
    var self = this;
    img.src = "http://www.antiquiet.com/wp-content/uploads/2011/03/Free-Trapper_Remasters_The-Kills-467x311.jpg";
    img.onload = function () {

      ctx = self.divLogo.nativeElement.getContext('2d');

      ctx.beginPath();
      ctx.arc(100, 100, 60, 0, 6.28, false); //draw the circle
      ctx.clip(); //call the clip method so the next render is clipped in last path
      ctx.stroke();
      ctx.closePath();
      ctx.drawImage(img, -190, 0);
    };

  }

}
